.header-wrapper {
  position: fixed;
  z-index: 3;
  width: -webkit-fill-available;

  .header-mobile {
    @include for-mobile-layout {
      justify-content: center;
    }
  }

  .main-header {
    background-color: $primary-white;
    height: 54px;

    .header-standard-view {
      @include for-narrow-layout {
        justify-content: space-between;
      }
    }
    .header-list-container {
      right: 1em;
      @include for-mobile-layout {
        position: fixed;
        bottom: 0;
        left: 0;
        padding-top: 5px;
        padding-bottom: 5px;
        width: -webkit-fill-available;
        background-color: rgba(var(--b3f, 250, 250, 250), 1);
      }
    }
    & .header-list {
      @include for-mobile-layout {
        justify-content: space-around;
      }
      height: 100%;
      margin-top: 4px;
      padding: 0;

      .header-list-explore {
        @include for-mobile-layout {
          display: none;
        }
      }

      .header-list-add{
        @include for-narrow-layout {
          display: none;
        }
      }
      .modal-btn {
        position: relative;
      }
      .logo-outer-border {
        border: 1px solid white;
        border-radius: 50%;
      }
      .modal-btn-border {
        position: relative;
        display: flex;
        border-radius: 50%;
        border: 1px solid rgba(var(--i1d, 38, 38, 38), 1);
      }

      & img {
        width: 22px;
      }
    }
    & .logo {
      @include for-mobile-layout {
        padding-right: 1rem;
      }
      padding-left: 1rem;
      text-transform: capitalize;
      font-family: "Billabong", sans-serif;
      display: flex;
      height: 100%;
      align-items: center;
      & h1 {
        font-size: 35px;
      }
    }

    .pos-abs {
      position: absolute;
      right: 0;
      top: 52px;
      z-index: 3;
    }

    .modal-diamond {
      height: 14px;
      width: 14px;
      margin-left: 92%;
      border: 1px solid rgba(var(--f23, 255, 255, 255), 1);
      transform: rotate(45deg);
      background: rgba(var(--d87, 255, 255, 255), 1);
      z-index: 1;
      position: absolute;
      top: -22px;
    }

    .modal-container {
      background: rgba(var(--d87, 255, 255, 255), 1);
      border-radius: 6px;
      margin-top: -16px;
      margin-right: -29px;
      position: relative;
      box-shadow: 10px 10px 10px 10px rgba(var(--jb7, 0, 0, 0), 0.0975);

      .modal {
        z-index: 1;
        width: 100%;
        height: 100%;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 20px;
        padding-right: 20px;

        > * {
          padding-bottom: 8px;
        }
        .modal-icon {
          margin-right: 12px;
          display: flex;
          align-items: center;
        }

        .modal-icon-txt {
          font-size: 14px;
          line-height: 18px;
          height: 28px;
          width: 158px;
          display: flex;
          align-items: center;
        }
      }
    }

    .modal-container .log-out {
      border-top: 1px solid;
      padding-bottom: 8px;
      padding-top: 8px;
      padding-left: 20px;
    }

    & a {
      @include for-narrow-layout {
        margin-right: 22px;
      }

      transition: all 0.2s;

      &:hover {
        color: $primary-light-clr;
      }
    }
  }
}
